// 
export const Navigator = {
	deviceVersion () {
		const u = navigator.userAgent
		const app = navigator.appVersion
		return {		// 
			userAgent: u,
			appVersion: app,
			trident: u.includes('Trident'),	 // IE
			presto: u.includes('Presto'), 	// opera
			webKit: u.includes('AppleWebKit'), 	// 
			gecko: u.includes('Gecko') && !u.includes('KHTML'), 	// 
			mobile: !!u.match(/AppleWebKit.*Mobile/i) || !!u.match(/MIDP|SymbianOS|NOKIA|SAMSUNG|LG|NEC|TCL|Alcatel|BIRD|DBTEL|Dopod|PHILIPS|HAIER|LENOVO|MOT-|Nokia|SonyEricsson|SIE-|Amoi|ZTE/),   // 
			ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), 	// ios
			android: u.includes('Android') || u.includes('Linux'), 	// androiduc
			iPhone: u.includes('iPhone') || u.includes('Mac'), 	// iPhoneQQHD
			iPad: u.includes('iPad'), 	// iPad
			webApp: !u.includes('Safari'), 	// web,
			language: (navigator.browserLanguage || navigator.language).toLowerCase()	// 
			// audroidVersion: u.substr(u.indexOf('Android') + 8, 3),
			// iosVersion: u.substr(u.indexOf('ios') + 8, 3)
		}
	}
}



