import {Navigator} from './Navigator.js'
// js
export const Utils = {
	// 
	// selectors document.querySelectorAll   div.div_img, div.content span, 
	// callback (dom):   dom dom
	setMoreEleStyle (selectors, callback) {
		Array.prototype.forEach.call(document.querySelectorAll(`${selectors}`), dom => {
			if (callback && typeof callback === 'function') {
				callback(dom)
			}
		})
	},
	// console  
	// text ;
	// isOneLine  13log ;
	// author 
	setConsole (text = 'this is console!', isOneLine = 1, author = '') {
		if (isOneLine) {
			console.log('')
			console.log(`%c${text}  ---  ${author}`, `background-image: url('data:image/svg+xml;base64,');background-size: 100%;background-image: -webkit-gradient(linear, 0% 50%, 100% 50%, color-stop(0%, #66cccc), color-stop(20%, #339999), color-stop(40%, #cccc99), color-stop(60%, #99ccff), color-stop(80%, #ccccff), color-stop(100%, #ff99cc));background-image: -moz-linear-gradient(left, #66cccc 0%, #339999 20%, #cccc99 40%, #99ccff 60%, #ccccff 80%, #ff99cc 100%);background-image: -webkit-linear-gradient(left, #66cccc 0%, #339999 20%, #cccc99 40%, #99ccff 60%, #ccccff 80%, #ff99cc 100%);background-image: linear-gradient(to right, #66cccc 0%, #339999 20%, #cccc99 40%, #99ccff 60%, #ccccff 80%, #ff99cc 100%);padding:20px 40px;color:#fff;font-size:12px;`)
			console.log('')
		} else {
			console.log(`%c${text}  ---  ${author}`, `background-image: url('data:image/svg+xml;base64,');background-size: 100%;background-image: -webkit-gradient(linear, 0% 50%, 100% 50%, color-stop(0%, #66cccc), color-stop(20%, #339999), color-stop(40%, #cccc99), color-stop(60%, #99ccff), color-stop(80%, #ccccff), color-stop(100%, #ff99cc));background-image: -moz-linear-gradient(left, #66cccc 0%, #339999 20%, #cccc99 40%, #99ccff 60%, #ccccff 80%, #ff99cc 100%);background-image: -webkit-linear-gradient(left, #66cccc 0%, #339999 20%, #cccc99 40%, #99ccff 60%, #ccccff 80%, #ff99cc 100%);background-image: linear-gradient(to right, #66cccc 0%, #339999 20%, #cccc99 40%, #99ccff 60%, #ccccff 80%, #ff99cc 100%);padding:0;color:#fff;font-size:12px;`)
		}
	},

	// promise  xyz
	moveImage () {
		const deviceVersion = Navigator.deviceVersion()
		return new Promise((resolve, reject) => {
			if (deviceVersion.ios) {
				resolve()
			} else if (deviceVersion.android) {
				reject()
			}
		})
	},

	// 
	formatDate (data, fmt) {
		var o = {
			// 'y+': data.getFullYear() + 1,                 // 
			'M+': data.getMonth() + 1,                 // 
			'd+': data.getDate(),                    // 
			'h+': data.getHours(),                   // 
			'm+': data.getMinutes(),                 // 
			's+': data.getSeconds(),                 // 
			'q+': Math.floor((data.getMonth() + 3) / 3), // 
			'S': data.getMilliseconds()             // 
		}
		if (/(y+)/.test(fmt)) {
			fmt = fmt.replace(RegExp.$1, (data.getFullYear() + '').substr(4 - RegExp.$1.length))
		}
		for (var k in o) {
			if (new RegExp('(' + k + ')').test(fmt)) {
				fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
			}
		}
		return fmt
		}
	}



